.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  display: none;
  width: 490px;
  padding: 33px 39px 36px;

  font-family: $secondary-font;
  color: $modal-text;

  background-color: $modal-bg;

  @media #{$screen-mobile} {
    width: 100%;
    height: 100vh;

    overflow-y: scroll;
  }

  &--opened {
    display: block;
  }

  h3 {
    @include font-mixin(28px, 39px);

    margin: 0;
    margin-bottom: 10px;

    font-family: $default-font;
    font-weight: 400;
  }

  p {
    @include font-mixin(16px, 22px);

    margin: 0;
    margin-bottom: 30px;
  }
}

.modal__form {
  input,
  textarea {
    width: 100%;
    padding: 15px 24px;

    background-color: rgba($modal-input-bg, 0.1);

    border: none;

    &::placeholder {
      @include font-mixin(16px, 22px);

      color: $feedback-text;
    }
  }
}

.modal__field {
  margin-bottom: 30px;
}

.modal__textarea {
  margin-bottom: 30px;

  textarea {
    height: 104px;
  }
}

.modal__footer {

}

.modal__btn {
  width: 100%;
  padding: 15px 20px;
  margin-bottom: 34px;
}

.modal__agreement {

}

.modal__check {
  position: relative;

  display: inline-flex;

  padding-left: 49px;

  @media #{$screen-tablet} {
    padding-left: 34px;
  }
}

.modal__checkbox {
  position: absolute;
  top: 0;
  left: 0;

  width: 17px;
  height: 16px;

  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid $white;

  .modal__checkbox-input:not(:disabled) + & {
    cursor: pointer;
  }

  .modal__checkbox-input:checked + & {
    background-image: url("../img/checkbox.svg");
  }

  .modal__checkbox-input:checked:focus + &,
  .modal__checkbox-input:focus + & {
    outline: 2px solid #4d90fe;
  }
}

.modal__close {
  position: absolute;
  right: 20px;
  top: 20px;

  width: 17px;
  height: 17px;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 10px;
    left: 0;

    width: 17px;
    height: 1px;

    background-color: $white;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}
